:root {
  --background: #f3c845;
  --background-Alpha: #f3c845aa;
  --text-primary: #010101;
  --first-accent: #0d21a1;
  --second-accent: #00d9c0;
  --third-accent: #f06543;
  --third-accent-a: #f06543aa;
  --fourth-accent: #ff4365;
  --white: #ffffff;
  --black: #000000;
  --slideshow-text: #ffffff;
  --nav-btn-home-page: #f3c845;
  --transparency: rgba(0, 0, 0, 0.5);
  --change-img-color: invert(0.05);
}
[data-theme="dark"] {
  --background: #272727;
  --background-Alpha: #212121;
  --text-primary: #747474;
  --first-accent: #ff652f;
  --second-accent: #ffe400;
  --third-accent: #14a76c;
  --third-accent-a: #14a76caa;
  --slideshow-text: #747474;
  --white: #000000;
  --black: #ffffff;
  --nav-btn-home-page: #ffe400;
  --grayscale: grayscale(1);
  --transparency: rgba(0, 0, 0, 0.5);
  --change-img-color: invert(0.4);
}
