#galleryView {
  margin-top: 10vh;
}

#galleryContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  position: relative;
}

#mainView {
  height: 50vh;
  width: 100vw;
  border-radius: 5px;
  z-index: 1;
  transition: all 1s;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
}

#mainView:hover {
  transform: scale(1.2);
  transition: all 1s;
}

#mainView > h3 {
  background-color: var(--third-accent);
  color: var(--black);
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
  max-width: 80vw;
}

#mainView > a {
  background-color: var(--third-accent);
  padding: 0.5rem;
  border-radius: 1rem;
}

.navBtns {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  border: none;
  position: absolute;
  opacity: 0.8;
  background-color: transparent;
  cursor: pointer;
  color: var(--black);
  z-index: 2;
}

.navBtns:hover {
  opacity: 1;
  transition: all 1s;
  background-color: var(--first-accent);
}

#navLeft {
  left: 0;
}

#navRight {
  right: 0;
}

@media screen and (max-height: 500px) {
  #mainView {
    flex-direction: row;
    height: calc(50vh - (3rem + 3vh) - 5vh);
  }
}

@media screen and (min-width: 768px) {
  #galleryContainer {
    justify-content: initial;
  }
  .navBtns {
    display: none;
  }
  #mainView {
    width: 60vw;
  }
  #leftView {
    height: 50vh;
    width: 15vw;
    border-radius: 5px;
    transform-origin: top right;
    transform: skewy(5deg);
    margin-right: 5vw;
    transition: all 1s;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    filter: grayscale(1);
    cursor: pointer;
  }
  #leftView:hover {
    opacity: 1;
    transition: all 1s;
  }
  #rightView {
    height: 50vh;
    width: 15vw;
    opacity: 0.5;
    border-radius: 5px;
    transform: skewy(-5deg);
    transform-origin: top left;
    margin-left: 5vw;
    transition: all 1s;
    background-position-x: center;
    background-size: cover;
    background-repeat: no-repeat;
    filter: grayscale(1);
    cursor: pointer;
  }

  #rightView:hover {
    opacity: 1;
    transition: all 1s;
  }
}
