#link-logo {
  text-decoration: none;
  color: white;
}

.logo-page {
  background-color: var(--background);
  position: absolute;
  top: 0;
  width: 100vw;
  height: 8rem;
  z-index: 99;
}

.logo-letter-a {
  border-bottom: 0.5rem solid var(--first-accent);
  height: 4.5rem;
  width: 6rem;
  left: 50%;
  position: absolute;
  border-radius: 20%;
  animation: myAnim 1s ease 0s 1 normal forwards;
}

.logo-letter-a:before,
.logo-letter-a:after {
  background: var(--third-accent);
  content: "";
  height: 6rem;
  position: absolute;
  top: 0.5rem;
  width: 1rem;
}
.logo-letter-a:before {
  transform: skew(-20deg, 0) translateX(0rem);
}
.logo-letter-a:after {
  left: 2%;
  transform: skew(20deg, 0) translateX(2rem);
}

.z-logo {
  position: absolute;
  border-bottom: 0.5rem solid var(--first-accent);
  height: 1rem;
  width: 5rem;
  top: 6rem;
  left: 50%;
  border-radius: 50%;
  transform: translateX(50%);
  animation: myAnim 1s ease 0s 1 normal forwards;
}

.z-logo::before {
  background-color: var(--first-accent);
  content: "";
  position: absolute;
  left: 1rem;
  top: -3rem;
  height: 4.5rem;
  transform: rotate(50deg) translateX(50%);
  width: 0.5rem;
  border-radius: 50%;
}

@media screen and (max-height: 400px) {
  .logo-page {
    height: 6rem;
  }
  .logo-letter-a {
    height: 4rem;
    width: 5rem;
    left: 50%;
    position: absolute;
    border-radius: 20%;
  }

  .logo-letter-a:before,
  .logo-letter-a:after {
    height: 5rem;
    width: 0.8rem;
  }

  .z-logo {
    height: 0;
    width: 4rem;
    top: 5.5rem;
  }
}

@keyframes myAnim {
  0% {
    width: 0rem;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
