.slideshow-container {
  position: absolute;
  bottom: 0;
  right: 50%;
  transform: translateX(50%);
  width: 100%;
  height: 25%;
  background-color: rgba(0, 0, 0, 0.5);
  color: var(--slideshow-text);
}

.mySlides {
  display: none;
  width: 80%;
  height: 100%;
  position: absolute;
  text-align: center;
  bottom: 0;
  right: 50%;
  transform: translateX(50%);
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.mySlides q {
  font-style: italic;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.prev,
.next {
  cursor: pointer;
  position: absolute;
  bottom: 25%;
  padding: 1rem;
  color: var(--slideshow-text);
  font-weight: bold;
  border-radius: 0 3px 3px 0;
  user-select: none;
  z-index: 1;
}

.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

.prev:hover,
.next:hover {
  background-color: var(--transparency);
  color: var(--slideshow-text);
}

.mentor {
  margin-bottom: 0.5rem;
}

.mentor > b {
  font-size: 1.5rem;
}

@media screen and (max-height: 400px) {
  .slideshow-container {
    height: 30%;
  }
}

@media screen and (min-height: 700px) and (min-width: 700px) {
  .slideshow-container {
    height: 15%;
  }
  .mySlides q {
    font-size: 2rem;
  }
  .mentor > b {
    font-size: 2.3rem;
  }
}

@media screen and (max-height: 700px) and (max-width: 300px) {
  .mySlides q {
    font-size: 1.2rem;
  }
  .mentor > b {
    font-size: 1.4rem;
  }
}

@media screen and (max-height: 374px) {
  .mySlides {
    flex-direction: row;
  }
  .mentor {
    margin: 1rem;
  }
}
