.work-page {
  padding-top: calc(3rem + 3vh);
  min-height: 100%;
  max-width: 100%;
  background-color: var(--background);
  text-align: center;
}

.work-page-btns {
  width: 100%;
  background-color: var(--background);
  position: absolute;
  padding: 2vh 0 4vh 0;
  bottom: 0;
  display: flex;
  justify-content: space-evenly;
}

.work-page .nav-button {
  text-decoration: none;
  color: var(--third-accent);
  border: solid 5px;
  padding: 0.5rem 1rem;
}
