#toggleContainer label {
  color: var(--text-primary);
}

#toggleContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateX(-1rem);
  cursor: pointer;
}

#toggle {
  margin: 0 1rem 0 1rem;
}

#outer3 {
  width: 10rem;
  height: 3rem;
  background-color: var(--third-accent);
  border-radius: 3px;
  transition: all 0.5s;
}

#slider3 {
  height: 3rem;
  width: 5rem;
  background-color: var(--first-accent);
  border-radius: 3px;
  transition: all 0.5s;
}

#slider3.active {
  -webkit-transform: translatex(50px);
  -ms-transform: translatex(50px);
  -o-transform: translatex(50px);
  transform: translatex(50px);
  transition: all 0.5s;
}

#outer3.outerActive {
  transition: all 0.5s;
}
