.home-page {
  height: 100%;
  background-color: var(--background);
  padding-top: 6rem;
  color: var(--text-primary);
}

.name {
  width: 100%;
  top: 20%;
  position: absolute;
  text-align: center;
  right: 50%;
  transform: translateX(50%);
}

.name span {
  font-size: 3rem;
}

#first-w {
  font-size: 5rem;
  color: var(--third-accent);
}

#first-w::after {
  content: "ðŸ‘‹";
  font-size: 3rem;
}

#third-w {
  display: inline-block;
  color: var(--first-accent);
  animation: myBounce 3s ease 0s 1 normal forwards;
}

#fifth-w {
  display: inline-block;
  font-size: 4rem;
  color: var(--third-accent);
  animation: myJello 3s ease 0s 1 normal forwards;
}

.mountain {
  position: fixed;
  margin-top: 8rem;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url("/public/img/mountain.png");
  background-size: cover;
  background-position: top;
  filter: var(--grayscale);
}

.home-page .nav-button {
  position: absolute;
  bottom: 40%;
  right: 50%;
  transform: translateX(50%);
  z-index: 1;
  text-decoration: none;
  color: var(--nav-btn-home-page);
  border: solid 5px;
  padding: 0.5rem 1rem;
  background-color: var(--transparency);
}

@media screen and (max-height: 400px) {
  .name span {
    font-size: 2rem;
  }
  #first-w {
    font-size: 4rem;
  }
  #fifth-w {
    font-size: 3rem;
  }
  .home-page .nav-button {
    font-size: 1.7rem;
    bottom: 35%;
    padding: 0.3rem 0.5rem;
  }
}

@media screen and (min-width: 900px) and (min-height: 700px) {
  .name span {
    font-size: 4rem;
  }
  #first-w {
    font-size: 6rem;
  }
  #fifth-w {
    font-size: 6rem;
  }
  .home-page .nav-button {
    font-size: 2.5rem;
    bottom: 35%;
    padding: 1rem 2rem;
  }
}

@keyframes myBounce {
  0% {
    animation-timing-function: ease-in;
    opacity: 1;
    transform: translateY(-45px);
  }

  24% {
    opacity: 1;
  }

  40% {
    animation-timing-function: ease-in;
    transform: translateY(-24px);
  }

  65% {
    animation-timing-function: ease-in;
    transform: translateY(-12px);
  }

  82% {
    animation-timing-function: ease-in;
    transform: translateY(-6px);
  }

  93% {
    animation-timing-function: ease-in;
    transform: translateY(-4px);
  }

  25%,
  55%,
  75%,
  87% {
    animation-timing-function: ease-out;
    transform: translateY(0px);
  }

  100% {
    animation-timing-function: ease-out;
    opacity: 1;
    transform: translateY(0px);
  }
}

@keyframes myJello {
  0% {
    transform: scale3d(1, 1, 1);
  }

  30% {
    transform: scale3d(0.75, 1.25, 1);
  }

  40% {
    transform: scale3d(1.25, 0.75, 1);
  }

  50% {
    transform: scale3d(0.85, 1.15, 1);
  }

  65% {
    transform: scale3d(1.05, 0.95, 1);
  }

  75% {
    transform: scale3d(0.95, 1.05, 1);
  }

  100% {
    transform: scale3d(1, 1, 1);
  }
}
