.contact-page {
  padding-top: 5rem;
  background: var(--background);
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info-contact {
  margin-top: 2rem;
  margin-left: 2rem;
}

.info-contact > div {
  display: flex;
  margin-bottom: 3rem;
  align-items: center;
}

.info-contact > .info-email > svg,
.info-contact > .info-phone > svg {
  animation: myRotation 2s ease 4s 1 normal forwards;
}

.info-contact svg {
  color: var(--first-accent);
  margin-right: 2rem;
  font-size: 3rem;
}

.info-contact p {
  color: var(--text-primary);
  font-weight: bolder;
  font-size: 1.8rem;
}

@media screen and (max-width: 315px) {
  .info-contact p {
    font-size: 1.5rem;
  }
}

@media screen and (min-width: 700px) and (min-height: 700px) {
  .contact-page {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .info-contact {
    width: 40%;
  }
}

@media screen and (min-width: 1200px) {
  .contact-page {
    justify-content: space-between;
  }
  .info-contact {
    width: auto;
  }
}
