@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500;900&display=swap");

html,
body {
  overflow-x: hidden;
  font-size: 10px;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-size: 2rem;
}

*::selection {
  background-color: var(--second-accent);
}
.home {
  height: 100vh;
}

.contents-page {
  height: 100vh;
}

#nextPage {
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.transition-wrapper {
  width: 100vw;
  height: 100vh;
}

.transition-wrapper a {
  position: absolute;
  right: 20px;
  bottom: 30px;
}
