.skills-section {
  width: 95%;
  height: 100%;
  padding-bottom: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.skill-container {
  display: grid;
  grid-template-columns: 4rem auto;
  grid-template-rows: 6rem;
}

.skill-container img {
  width: 3rem;
  filter: var(--change-img-color);
  justify-self: center;
  align-self: center;
}

.skill-container svg {
  justify-self: center;
  align-self: center;
  font-size: 3rem;
}

.skill-container .progressBarFancyContainer {
  justify-self: center;
  align-self: center;
}

.skill-container .progressBarFancyContainer .labelScoreContainer div {
  font-size: 1.5rem;
}

.progressBarFancyContainer .label,
.progressBarFancyContainer .number {
  color: var(--text-primary);
}

@media screen and (min-width: 700px) and (min-height: 700px) {
  .skills-section {
    width: 70%;
    margin-left: 15vw;
  }
  .skill-container {
    grid-template-columns: 6rem auto;
    grid-template-rows: 7rem;
  }
  .skill-container img {
    width: 4rem;
  }
  .skill-container svg {
    font-size: 4rem;
  }
  .skill-container .progressBarFancyContainer .labelScoreContainer div {
    font-size: 2rem;
  }
}
