.somethings {
  width: 100%;
  height: 100%;
  padding-top: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  padding-bottom: 2rem;
}

.somethings .flippy-cardContainer-wrapper {
  width: 25rem;
  height: 25rem;
  text-align: center;
  animation: myRotation 2s ease 0s 1 normal forwards;
  animation-delay: 1s;
}

.somethings .flippy-card.flippy-front {
  background-color: var(--background-Alpha);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  color: var(--text-primary);
}

.somethings .flippy-card.flippy-front img {
  width: 13rem;
  filter: var(--change-img-color);
}

.somethings .flippy-card.flippy-front h3 {
  color: var(--third-accent);
}

.somethings .flippy-card.flippy-back {
  background-color: var(--background-Alpha);
  color: var(--text-primary);
  display: flex;
  align-items: center;
}

.somethings .flippy-back h4 {
  font-size: 1.8rem;
  word-spacing: 1px;
  line-height: 2.5rem;
}

.btn-cv {
  text-decoration: none;
  border: solid 5px;
  padding: 1rem 2rem;
  background-color: transparent;
  color: var(--first-accent);
  animation: myRotation 2s ease 1s 1 normal forwards;
}

.btn-cv a {
  text-decoration: none;
  color: var(--first-accent);
}

@media screen and (min-width: 700px) and (min-height: 700px) {
  .somethings {
    gap: 5rem;
    padding-top: 6rem;
    padding-bottom: 5rem;
  }
  .somethings .flippy-cardContainer-wrapper {
    width: 30rem;
    height: 30rem;
  }
  .somethings .flippy-card.flippy-front {
    justify-content: space-around;
  }
}

@keyframes myRotation {
  0%,
  100% {
    transform: rotate(0deg);
    transform-origin: 50% 50%;
  }

  10% {
    transform: rotate(8deg);
  }

  20%,
  40%,
  60% {
    transform: rotate(-10deg);
  }

  30%,
  50%,
  70% {
    transform: rotate(10deg);
  }

  80% {
    transform: rotate(-8deg);
  }

  90% {
    transform: rotate(8deg);
  }
}
