.map-container {
  margin-top: 5rem;
}

.map-container > div {
  width: 100vw;
  height: 50rem;
  position: relative;
}

@media screen and (min-width: 700px) and (min-height: 700px) {
}

@media screen and (min-width: 1200px) {
  .map-container {
    margin-left: 2rem;
    margin-top: 0;
    flex-grow: 2;
  }
  .map-container > div {
    width: 100%;
    height: calc(100vh - 10rem);
  }
}
