#tilesView {
  margin-top: 10vh;
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

#tilesView .flippy-container {
  height: 50vh;
  width: 80vw;
  margin-bottom: 3rem;
}

#tilesView .flippy-container:last-child {
  margin-bottom: 20vh;
}

#tilesView .flippy-front {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#tilesView .flippy-back {
  background-color: var(--third-accent);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

#tilesView .titleWork {
  text-align: center;
  color: var(--white);
}

@media screen and (min-width: 768px) {
  #tilesView {
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 5rem;
  }

  #tilesView .flippy-container {
    width: auto;
  }

  #tilesView .flippy-cardContainer {
    width: 30vw;
  }
}
